  overflow: auto;
}

/**
 * Don't inherit the `font-weight` (applied by a rule above).
 * NOTE: the default cannot safely be changed in Chrome and Safari on OS X.
 */
optgroup {
  font-weight: bold;
}

/* Tables
   ========================================================================== */
/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.clearfix {
  zoom: 1;
}

.clearfix:before, .clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.hidden {
  display: none !important;
  visibility: hidden;
}

.visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.visuallyhidden.focusable:active,
.visuallyhidden.focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

.invisible {
  visibility: hidden;
}

.image-replacement {
  text-indent: 100%;
  white-space: nowrap;
  overflow: hidden;
}

/* Screenreader Text
@see https://make.wordpress.org/accessibility/2015/02/09/hiding-text-for-screen-readers-with-wordpress-core/
*/
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  left: -999em;
}

.screen-reader-text:focus {
  clip: auto !important;
  display: block;
  height: auto;
  left: 5px;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */
}

html {
  font-size: 100%;
  background-color: #fff;
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
}

body, button, input, select, textarea {
  color: #565656;
}

body {
  margin: 0 auto;
  background-color: #fff;
  font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
  min-width: 320px;
  overflow-x: hidden;
}

::-moz-selection, ::selection {
  background: #12b1e5;
  color: #fff;
  text-shadow: none;
}

@-webkit-viewport {
  width: device-width;
}

@-moz-viewport {
  width: device-width;
}

@-ms-viewport {
  width: device-width;
}

@-o-viewport {
  width: device-width;
}

@viewport {
  width: device-width;
}

/* Default Link Styles */
a {
  color: #12b1e5;
  line-height: inherit;
}

a:hover, a:focus {
  color: #0e89b1;
  text-decoration: none;
}

a img {
  display: block;
  margin-bottom: 0;
  border: none;
}

a:link {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0.3);
}

.content-wrap {
  max-width: 1140px;
  width: 100%;
  padding: 0 1.25rem;
  margin: 0 auto;
}

/*	@font-face {
    	font-family: 'Font Name';
    	src: url('../fonts/font-name.eot');
    	src: url('../fonts/font-name.eot?#iefix') format('embedded-opentype'),
             url('../fonts/font-name.woff') format('woff'),
             url('../fonts/font-name.ttf') format('truetype'),
             url('../fonts/font-name.svg#font-name') format('svg');
    	font-weight: normal;
    	font-style: normal;
	}
*/
h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
  font-weight: bold;
  font-style: normal;
  color: #222;
  margin: .2rem 0 .5rem;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a, .h1 a, .h2 a, .h3 a, .h4 a, .h5 a, .h6 a {
  text-decoration: none;
}

h1,
h2 {
  margin-top: 0;
}

h1, .h1 {
  font-size: 3rem;
  line-height: 1.05em;
}

h2, .h2 {
  font-size: 2.25rem;
  line-height: 1.25em;
}

h3, .h3 {
  font-size: 1.75rem;
  font-weight: normal;
  line-height: 1.25em;
}

h4, .h4 {
  font-size: 1.25rem;
  line-height: 1.22222222em;
}

h5, .h5 {
  font-size: 1.125rem;
  line-height: 1.25em;
}

h6, .h6 {
  font-size: 0.875rem;
  line-height: 1.25em;
}

p {
  margin-top: 0;
}

ul, ol, dl {
  line-height: 1.33;
  margin: 0 1.5em 1.5em 0;
  padding: 0 0 0 1.5em;
}

ul {
  list-style-type: disc;
}

ol {
  list-style-type: decimal;
}

ul li, ol li {
  margin: .8em 0 0;
}

ul li ul, ul li ol, ol li ul, ol li ol {
  margin-left: 1.25em;
}

dl {
  padding-left: 14px;
}

dl dt {
  margin-bottom: .45em;
  font-weight: 600;
}

dl dd {
  margin-bottom: .75em;
  margin-left: 0;
}

table {
  font-size: 1rem;
  margin: .75em 0 1.5em;
  background-color: #fff;
  border: 1px solid #000;
  text-align: left;
}

table th {
  padding: 14px 3% 12px;
  background-color: grey;
  font-weight: 600;
  line-height: 1;
}

table th ~ th {
  border-left: 1px solid grey;
}

table tbody tr ~ tr {
  border-top: 1px solid #000;
}

table td {
  padding: 12px 3% 10px;
  line-height: 1.35;
}

table td ~ td {
  border-left: 1px solid #000;
}

form input:focus,
form textarea:focus,
form select:focus {
  outline: 0;
}

form input,
form textarea {
  width: 100%;
  padding: 10px;
}

form textarea {
  resize: none;
}

form input[type=submit] {
  background-color: transparent;
  border: 0;
  outline: 0;
}

form input::-webkit-input-placeholder {
  color: #7f7f7f;
}

form input:-moz-placeholder {
  color: #7f7f7f;
  opacity: 1;
}

form input::-moz-placeholder {
  color: #7f7f7f;
  opacity: 1;
}

form input:-ms-input-placeholder {
  color: #7f7f7f;
}

.nav {
  margin: 0;
  padding: 0;
  list-style: none;
}

.nav li {
  float: left;
  margin: 0;
}

.nav li a {
  padding: 10px 20px;
  display: block;
}

.nav li ul {
  display: none;
  visibility: hidden;
}

.nav li:hover ul, .nav li:focus ul {
  display: block;
  visibility: visible;
}

@media (min-width: 32.5rem) {
  .single .book {
    padding-left: 10;
    padding-right: 10;
    width: 33.33333%;
    float: left;
    margin: 2em 0;
  }
}
